import "core-js/modules/es7.array.includes";
import "core-js/modules/es6.string.includes";
import "core-js/modules/es6.object.assign";
import "core-js/modules/es6.regexp.split";
import "core-js/modules/es6.string.starts-with";
import "core-js/modules/es6.regexp.search";
import "core-js/modules/es6.array.iterator";
import "core-js/modules/es6.promise";
import "core-js/modules/es7.promise.finally";

/**
 * A collection of functions for handling authentication within the app.
 */
import { CognitoAuth } from "amazon-cognito-auth-js";
import appSyncConfig from '@/config';
import router from '@/router';
var COGNITO_ORIGINATOR_PAGE = "CognitoOriginatorPage";
var COGNITO_LAST_RELOADED_PAGE = "CognitoLastReloadedPage";
/**
 * Any page that requests data from Apollo will have to get a token from
 * Cognito via Aplify.Auth.
 * If a token is not in storage, invalid or cannot be refreshed Amplify will
 * Throw an error, which gets us here.
 */

export function cognitoRelogin() {
  console.log("cognitoRelogin"); // Check if the current page is a callback with a code from Cognito
  // Vue adds the # at the end before the code is processed - remove it

  if (window.location.search != "") {
    parseCognitoResponse();
  } else {
    //Store the current URL to return to after logging in
    localStorage.setItem(COGNITO_ORIGINATOR_PAGE, window.location.href); //redirect to Cognito login page

    window.location.href = "https://".concat(appSyncConfig.AppWebDomain, "/login?redirect_uri=").concat(appSyncConfig.RedirectUriSignIn, "&client_id=").concat(appSyncConfig.ClientId, "&response_type=code");
  }
}
/**
 * Checks the URL for ?query= and return true if it's ?code=... from Cognito
 * or navigate to the root of the app to get rid of the query string.
 * Returns false otherwise.
 */

export function isCognitoCode() {
  var urlQuery = window.location.search; //console.log("isCognitoCode URL search: " + urlQuery)
  // does it have any query part of the URL?

  if (!urlQuery) return false; // does it look like a cognito response?

  if (urlQuery.startsWith("?code=")) return true;
  return false;
}
/**
 *
 * @param {String} curUrl A sanitised URL with Cognito callback response code
 * @returns {void} Navigates to a different page. Success: original page before the login or Home. Failure: AuthError page.
 * @description Ideally, the callback URL needs to be handled by Amplify, but getting it 
 * to work with Vue's # was challenging, hence this shortcut.
 * Amplify runs into problems with the # in the URL in the callback as well.
 * This is why we have to use amazon-cognito-auth-js library. Amplify uses that
 * same library behind the hood, but it isn't exported.
 */

export function parseCognitoResponse() {
  // The response may look like this http://localhost:8080/?code=002749f2-5cbf-4c80-87b2-c81c93938657#/home-public
  // Cognito lib won't work if there is a # after the code.
  var curUrl = window.location.href.split("#")[0];
  console.log(window.location.href);
  console.log(curUrl);
  var cognitoAuthParams = Object.assign({
    ClientId: appSyncConfig.ClientId,
    UserPoolId: appSyncConfig.UserPoolId,
    AppWebDomain: appSyncConfig.AppWebDomain,
    TokenScopesArray: appSyncConfig.TokenScopesArray,
    RedirectUriSignIn: appSyncConfig.RedirectUriSignIn,
    RedirectUriSignOut: appSyncConfig.RedirectUriSignOut
  });
  var cognitoAuthClient = new CognitoAuth(cognitoAuthParams);
  var ignoreFailure = false;
  cognitoAuthClient.userhandler = {
    // this handler is called from inside parseCognitoWebResponse()
    onSuccess: function onSuccess() {
      //console.log("parseCognitoResponse.onSuccess")
      ignoreFailure = true; // navigate to the page the user tried to access before the login

      restoreUserLocationAfterLogin(router);
    },
    onFailure: function onFailure() {
      //console.log("parseCognitoResponse.onFailure")
      if (ignoreFailure) {
        //console.log("ignoreFailure")
        return;
      } // check if we can reload the page


      var lastReloaded = localStorage.getItem(COGNITO_LAST_RELOADED_PAGE);

      if (lastReloaded != window.location.href && window.location.search != "") {
        //console.log("reloading the page")
        localStorage.setItem(COGNITO_LAST_RELOADED_PAGE, window.location.href);
        window.location.reload();
      } else {
        //console.log("already reloaded - navigating away")
        window.location.href = window.location.href.split("?")[0].split("#")[0];
      }
    }
  }; // try parsing the response - it will request and store token data in the background
  // ideally, there should be a session after it runs, but often we need to try getting a new one

  cognitoAuthClient.parseCognitoWebResponse(curUrl); //alert("Response parsed")
}
/**
 * Navigate back to the page before the login or HOME if there is none.
 * Not using Vuew Router.
 * 
 * EXPORT - the function is used in vAuthError. Find a better way.
 */

export function restoreUserLocationAfterLogin() {
  //console.debug("restoreUserLocationAfterLogin")
  var origUrl = localStorage.getItem(COGNITO_ORIGINATOR_PAGE);
  localStorage.removeItem(COGNITO_ORIGINATOR_PAGE); // remove prevent a loop

  if (origUrl && origUrl != "") {
    //console.debug("Navigating to URL from local storage: " + origUrl)
    window.location.href = origUrl;
  } else if (window.location.href.includes("?")) {
    //console.debug("URL contains ? - trying to remove by navigating to root: " + window.location.href)
    window.location.href = window.location.href.split("?code=")[0];
  } else {// there is really nothing else to do - stay as is, the URL is good
    //console.debug("URL is OK - no action taken")
  }
}