import Vue from 'vue'
import App from '@/App.vue'
import router from '@/router'
import Vuetify from 'vuetify'
import VuetifyStyles from "./styles/Vuetify.js"
import VueLocalForage from 'vue-localforage'

import "@/monitoring.js"
import "@/filters.js"
import { store } from "@/store.js"

import AWSAppSyncClient from "aws-appsync"
import * as localForage from "localforage";
import VueApollo from 'vue-apollo'
import appSyncConfig from '@/config'
import Amplify, { Auth } from "aws-amplify"
import { cognitoRelogin } from '@/appAuth.js'

import * as VueGoogleMaps from 'vue2-google-maps'

// Init Amplify for authentication
//Amplify.Logger.LOG_LEVEL = 'DEBUG'
Amplify.configure({
  Auth: {
    region: appSyncConfig.region,
    userPoolId: appSyncConfig.UserPoolId,
    userPoolWebClientId: appSyncConfig.ClientId,
    oauth: {
      domain: appSyncConfig.AppWebDomain,
      scope: appSyncConfig.TokenScopesArray,
      redirectSignIn: appSyncConfig.RedirectUriSignIn,
      redirectSignOut: appSyncConfig.RedirectUriSignOut,
    }
  }
});

// Init AppSync provider
const client = new AWSAppSyncClient({
  url: appSyncConfig.graphqlEndpoint,
  region: appSyncConfig.region,
  auth: {
    type: appSyncConfig.authenticationType,
    // the token is requested every time a request is being sent and is refreshed automatically
    jwtToken: async () => {
      try {
        return (await Auth.currentSession()).getIdToken().getJwtToken()
      }
      catch (e) {
        cognitoRelogin() // Navigate to the hosted login page for a fresh login attempt
      }
    }
  },
  offlineConfig: {
    storage: localForage,
  },
  disableOffline: false,
}, {
    defaultOptions: {
      watchQuery: {
        fetchPolicy: 'network-only',
      }
    }
  })

const appsyncProvider = new VueApollo({
  defaultClient: client,
  defaultOptions: {
    // apollo options applied to all queries in components
    $query: {
      fetchPolicy: 'network-only',
      errorPolicy: appSyncConfig.errorPolicy
    },
  },
})


Vue.use(VueGoogleMaps, {
  load: {
    key: appSyncConfig.GoogleMapsKey
  }
})

// Change the document title depending on the page. It will be saved in browser history.
router.afterEach((to) => {
  if (to.params.devId) {
    document.title = "Cam " + to.params.devId
  }
  else {
    document.title = appSyncConfig.appName
  }
})

// LocalStorage can be accessed via this.$getItem(key, callback), this.$setItem(key, value, callback)
// see https://github.com/shidianxia/vue-localforage#readme
Vue.use(VueLocalForage)

Vue.use(Vuetify, VuetifyStyles)
Vue.use(VueApollo)
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  apolloProvider: appsyncProvider,
  render: h => h(App)
})

