import "core-js/modules/es6.regexp.replace";
import Vue from 'vue';
Vue.filter('tsToTimeDate', function (value) {
  if (!value) return value;
  var ts = value.replace(/-/g, "").replace(/:/g, "");
  return ts.slice(9, 11) + ':' + ts.slice(11, 13) + '  ' + ts.slice(6, 8) + '/' + ts.slice(4, 6) + '/' + ts.slice(2, 4);
});
Vue.filter('tsToMMMDDHHmm', function (value) {
  if (!value) return value;
  var ts = value.replace(/-/g, "").replace(/:/g, "");
  return ts.slice(6, 8) + ' ' + monthToText(ts.slice(4, 6)) + " " + ts.slice(9, 11) + ':' + ts.slice(11, 13);
});
Vue.filter('tsToTime', function (value) {
  if (!value) return value;
  var ts = value.replace(/-/g, "").replace(/:/g, "");
  return ts.slice(9, 11) + ':' + ts.slice(11, 13);
});
Vue.filter('tsToDate', function (value) {
  if (!value) return value;
  var ts = value.replace(/-/g, "").replace(/:/g, "");
  return ts.slice(6, 8) + '/' + ts.slice(4, 6) + '/' + ts.slice(0, 4);
}); // 25 Apr 2019

Vue.filter('tsToDateTextMonth', function (value) {
  if (!value) return value;
  var ts = value.replace(/-/g, "").replace(/:/g, "");
  return ts.slice(6, 8) + ' ' + monthToText(ts.slice(4, 6)) + ' ' + ts.slice(0, 4);
}); // 25 Apr 03h

Vue.filter('tsTo_DDMMMHHh', function (value) {
  if (!value) return value;
  return value.slice(8, 10) + ' ' + monthToText(value.slice(5, 7)) + ' ' + value.slice(11, 13) + "h";
}); // 25 Apr

Vue.filter('tsTo_DDMMM', function (value) {
  if (!value) return value;
  return value.slice(8, 10) + ' ' + monthToText(value.slice(5, 7));
});

function monthToText(m) {
  switch (m) {
    case "01":
      return "Jan";

    case "02":
      return "Feb";

    case "03":
      return "Mar";

    case "04":
      return "Apr";

    case "05":
      return "May";

    case "06":
      return "Jun";

    case "07":
      return "Jul";

    case "08":
      return "Aug";

    case "09":
      return "Sep";

    case "10":
      return "Oct";

    case "11":
      return "Nov";

    case "12":
      return "Dec";
  }
}