  <router-link
    v-if="showImg && this.state"
    :to="{name:'device',params:{devId:this.state.items[0].device}}"
  >
    <v-img
      v-if="hydrated"
      :src="`https://${mediaBaseDomain}/${this.state.items[0].device}/resized/fhd/${this.state.media.name}.jpg`"
      lazy-src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mOcWQ8AAbcBGqb119EAAAAASUVORK5CYII="
      height="300"
      width="100%"
    >
      <template v-slot:placeholder>
        <v-layout fill-height align-center justify-center ma-0>
          <v-progress-circular indeterminate color="blue-grey"></v-progress-circular>
        </v-layout>
      </template>
    </v-img>
  </router-link>
</template>



<script>
import appSyncConfig from "@/config";

export default {
  name: "DeviceImg",
  props: { state: Object, showImg: Boolean },
  computed: {
    hydrated: function() {
      return this.state.media != null;
    },
    mediaBaseDomain: function() {
      return appSyncConfig.mediaBaseDomain
    }
  }
};
</script>