export default {
  name: "DeviceState",
  props: {
    state: Object
  },
  computed: {
    hydrated: function hydrated() {
      return this.state.device != null;
    },
    item: function item() {
      return this.state.items[0];
    },
    volt: function volt() {
      try {
        var v = Number.parseInt(this.state.items[0].volt);
        if (v > 0) return (v / 1000).toString() + " v";
      } catch (e) {
        return "n/a";
      }
    },
    temp: function temp() {
      try {
        var v = Number.parseInt(this.state.items[0].temp);
        if (v > 0) return (v / 10).toString() + " ºC";
      } catch (e) {
        return "n/a";
      }
    },
    mem: function mem() {
      try {
        var v = Number.parseInt(this.state.items[0].mem);
