export default {
  name: "home-public",
  data: function data() {
    return {};
  },
  created: function created() {
    // navigate to the home page after a timeout
    var router = this.$router;
    setTimeout(function () {
      router.push({
        name: 'home'
      });
    }, 3000);
  }
};