      this.error = false;
      this.saving = false;
      this.expand = [false];
      this.select = {
        text: v.toString(),
        value: v.toString()
      };
      this.newValue = v.toString();
      this.hasValue = v && v.toString() != "";
    }
  },
  computed: {
    canSave: function canSave() {
      var v = this.newValue != this.select.value.toString();
