        variables: {
          device: localDevId,
          setting: localSetting,
          value: localValue
        },
        update: function update(store, _ref) {
          var putDeviceConfig = _ref.data.putDeviceConfig;
          var retValue = putDeviceConfig.config[localSetting].toString();
          that.select.value = retValue;
          that.newValue = retValue;
        }
      }).then(function () {
        that.saving = false;
        setTimeout(function () {
          that.expand = [false];
        }, 1000);
      }).catch(function (error) {
        that.saving = false;
        that.error = true;
        console.log("mutate.error for ".concat(localSetting));
