/* This file is a proxy for .env, .env.* with the actual setting values.
They can also be names .env.local or .env.*.local
See https://cli.vuejs.org/guide/mode-and-env.html#environment-variables for more info
on the file format.

Example of .env values

VUE_APP_graphqlEndpoint=https://7eq2atgg...yx6kaf3mj7ny.appsync-api.us-east-1.amazonaws.com/graphql
VUE_APP_region=us-east-1
VUE_APP_UserPoolId=us-east-1_8L0.....K2

*/

export default  {
    graphqlEndpoint: process.env.VUE_APP_graphqlEndpoint, // URL from AppSync AWS console, e.g. https://7eq2at...7ny.appsync-api.us-east-1.amazonaws.com/graphql
    mediaBaseDomain: process.env.VUE_APP_mediaBaseDomain, // domain from S3 or Cloudfront, e.g. bucket-name.s3.amazonaws.com
    region: process.env.VUE_APP_region, // from AppSync AWS console, e.g. us-east-1 
    authenticationType: "AMAZON_COGNITO_USER_POOLS", // NONE, API_KEY, AWS_IAM, AMAZON_COGNITO_USER_POOLS, OPENID_CONNECT
    UserPoolId: process.env.VUE_APP_UserPoolId, // from AWS Cognito console, e.g. us-east-1_8L0...2
    ClientId: process.env.VUE_APP_ClientId, // from AWS Cognito console, e.g. 4852gt8...o148p0o
    AppWebDomain: process.env.VUE_APP_AppWebDomain, // from AWS Cognito console, e.g. sensorable.auth.us-east-1.amazoncognito.com
    TokenScopesArray: ["email","profile","openid"], // must match Cognito config
    RedirectUriSignIn: process.env.VUE_APP_RedirectUriSignIn, // must match Cognito config
    RedirectUriSignOut: process.env.VUE_APP_RedirectUriSignOut, // must match Cognito config
    GoogleMapsKey: process.env.VUE_APP_GoogleMapsKey, // from Google API console, e.g. AIzaS...76SY
    errorPolicy: process.env.VUE_APP_errorPolicy, // See https://www.apollographql.com/docs/react/features/error-handling/
    appName: process.env.VUE_APP_appName, // Homepage title, e.g. Cam Viewer
    SentryDSN: process.env.VUE_APP_SentryDSN, // E.g. https://26f07...23c8d8@sentry.io/14...95
    MatomoHost: process.env.VUE_APP_MatomoHost, // E.g. https://sensorable.matomo.cloud/
    MatomoSiteId: process.env.VUE_APP_MatomoSiteId, // 1,2,etc from matomo settings
};