<template>
  <v-dialog
    v-model="dialogState"
    hide-overlay
    persistent
    transition="dialog-transition"
    scrollable
    :fullscreen="$vuetify.breakpoint.smAndDown"
    width="600"
    max-width="600"
  >
    <v-card>
      <v-card-title class="pa-0 ma-0">
        <v-btn
          small
          v-if="!vidUrl && !mediaUploadItem.fileReqUtc"
          :disabled="downloadDisabled"
          color="blue-grey"
          class="white--text ml-3"
          @click="mediaRequest()"
        >
          Download video
          <v-icon size="12" right dark class="fal">fa-download</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn flat icon color="blue-grey" @click.stop="closeDialog()">
          <v-icon>close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text class="pt-0">
        <video
          v-if="vidUrl && vidUrl!=''"
          v-on:canplay="updatePlaybackSpeed"
          ref="dialogVideo"
          controls
          preload="auto"
          muted
          :src="`https://${mediaBaseDomain}/full/${devId}/${vidUrl}.mp4`"
          width="100%"
          height="auto"
        ></video>
        <v-img
          class="clickable"
          v-else-if="mediaUploadItem.name"
          :src="`https://${mediaBaseDomain}/${devId}/resized/fhd/${mediaUploadItem.name}.jpg`"
          width="100%"
          @click="openImageInNewWindow(`https://${mediaBaseDomain}/${devId}/resized/fhd/${mediaUploadItem.name}.jpg`)"
        >
          <v-container
            fill-height
            v-if="(mediaUploadItem.fileReqUtc && fileRequestedAlert) || downloadError"
          >
            <v-layout fill-height>
              <v-flex xs12 align-end flexbox>
                <v-alert
                  :value="downloadError"
                  type="error"
                >Something went wrong with the download request. Try refreshing the entire page and requesting again.</v-alert>

                <v-alert
                  :value="fileRequestedAlert"
                  type="info"
                  @click.stop="fileRequestedAlert = false"
                >
                  <h3 class="headline">Video file requested</h3>
                  <div>
                    <p>It may take between a few seconds and a few minutes to download the video from the camera. You can request videos for other cars while you wait.</p>
                    <p>Videos are downloaded over a cellular network and may incur additional cost.</p>
                    <p>
                      <strong>Time requested</strong>
                      : {{ lastRequestMsg() }}
                    </p>
                  </div>
                  <v-spacer></v-spacer>
                  <v-btn
                    small
                    v-if="allowReDownload"
                    color="blue-grey"
                    class="white--text ml-3"
                    @click="mediaRequest()"
                  >
                    Re-request video
                    <v-icon size="12" right dark class="fal">fa-download</v-icon>
                  </v-btn>
                  <v-btn
                    small
                    right
                    color="blue-grey"
                    class="white--text ml-3"
                    @click="fileRequestedAlert = false"
                  >Dismiss</v-btn>
                </v-alert>
              </v-flex>
            </v-layout>
          </v-container>
        </v-img>
      </v-card-text>
      <v-card-actions>
        <v-container class="pa-0 ma-0">
          <v-layout row wrap>
            <v-flex xs12 sm4 class="pa-1 text-xs-center">
              <h3
                v-if="mediaUploadItem.speed > 0"
                class="headline mb-0 text-xs-center dialog-plate"
              >
                <span v-if="mediaUploadItem.plate">
                  <router-link
                    :to="{name:'device-plate',params:{devId:devId, plate:mediaUploadItem.plate}}"
                  >
                    <span>{{mediaUploadItem.plate}}</span>
                  </router-link>
                </span>
              </h3>
              <span class="mx-2 grey--text caption">{{ mediaUploadItem.name | tsToTimeDate }}</span>
            </v-flex>
            <v-flex xs12 sm8 class="pa-1">
              <p class="ma-0 pa-0 text-xs-center grey--text">Speed, km/h</p>
              <acceleration-chart :devId="devId" :timestampFrom="tsFrom" :timestampTo="tsTo"/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>



<script>
import AccelerationChart from "@/components/AccelerationChart.vue";
import appSyncConfig from "@/config";
import * as moment from "moment";
import {
  DEV_FILE_REQUEST,
  LIST_MEDIA_UPLOADS_WITH_SPEED,
  LIST_MEDIA_UPLOADS_BY_PLATE
} from "@/assets/graph-ql.js";

export default {
  name: "MediaDialog",
  components: { AccelerationChart },
  data: () => {
    return {
      fileRequestedAlert: true,
      downloadDisabled: false,
      downloadError: false
    };
  },
  props: {
    devId: String
  },
  computed: {
    itemTimestamp() {
      this.downloadDisabled = false;
      return this.$route.query.vid;
    },

    mediaBaseDomain: function() {
      return appSyncConfig.mediaBaseDomain
    },

    mediaUploadItem: function() {
      // pre-checks for existing data
      if (!this.itemTimestamp) return {}; // no mediaUploadItem passed
      if (!this.$store.state.dbMediaUploadsWithSpeed.items) return {}; // no data loaded from DB

      // a container for an item from the DB
      // matching mediaUploadItem by timestamp
      let elMatch = {};

      // find a match in the latest set of data
      const match = this.$store.state.dbMediaUploadsWithSpeed.items.some(
        function(el) {
          if (el.timestamp == this.itemTimestamp) {
            // match found - grab the data and exit
            elMatch = el;
            return true;
          }
        },
        this
      );

      return elMatch;
    },

    dialogState: {
      get: function() {
        // check if there is no media assigned to the dialog
        if (this.itemTimestamp && this.mediaUploadItem.name) return true;
        return false;
      },
      set: function() {
        // nothing to do here
      }
    },

    tsFrom: function() {
      if (!this.mediaUploadItem.name) return "";

      let ts = this.mediaUploadItem.name.split("_")[0];
      return (
        ts.slice(0, 4) +
        "-" +
        ts.slice(4, 6) +
        "-" +
        ts.slice(6, 11) +
        ":" +
        ts.slice(11, 13) +
        ":" +
        ts.slice(13, 15)
      );
    },

    tsTo: function() {
      if (!this.mediaUploadItem.name) return "";

      let ts = this.mediaUploadItem.name.split("_")[1];
      return (
        ts.slice(0, 4) +
        "-" +
        ts.slice(4, 6) +
        "-" +
        ts.slice(6, 11) +
        ":" +
        ts.slice(11, 13) +
        ":" +
        ts.slice(13, 15)
      );
    },

    allowReDownload: function() {
      // allow to download if the last request was more than ... ago
      if (
        moment.utc() >
        moment.utc(this.mediaUploadItem.fileReqUtc).add(3, "minutes")
      )
        return true;
    },

    vidUrl: function() {
      if (
        this.mediaUploadItem.type &&
        this.mediaUploadItem.type.includes("mp4")
      )
        return this.mediaUploadItem.name;
    }
  },

  methods: {
    openImageInNewWindow: function(imgUrl) {
      // only open the image if the download alert is not showing
      if (!this.fileRequestedAlert || !this.mediaUploadItem.fileReqUtc)
        window.open(imgUrl, "_blank");
    },

    lastRequestMsg: function() {
      // return a user-friendly message of when the last request was made
      const now = moment.utc();
      const then = moment.utc(this.mediaUploadItem.fileReqUtc);
      const diffMin = now.diff(then, "seconds");
      if (diffMin < 20) return "just now";
      if (diffMin < 70) return "a minute ago";
      if (diffMin < 600) return Math.floor(diffMin / 60) + " minutes ago";
      return then.local().format("HH:mm, DD MMM YYYY");
    },

    mediaRequest() {
      this.downloadDisabled = true;
      const localDevId = this.devId;
      const localFileName = this.mediaUploadItem.name + ".mp4";

      this.$apollo
        .mutate({
          mutation: DEV_FILE_REQUEST,
          variables: {
            device: localDevId,
            filename: localFileName
          },

          update: (store, { data: { reqMediaDownload } }) => {
            // check if there was a error
            if (!reqMediaDownload) {
              this.fileRequestedAlert = false;
              this.downloadError = true;
              return;
            }

            this.$store.commit("setLastMediaUploadsReqTsUtc", reqMediaDownload);

            this.fileRequestedAlert = true;
            this.mediaUploadItem.fileReqUtc = reqMediaDownload;
          }
        })
        .then(() => {
          // Result
        })
        .catch(error => {
          console.log(`mutate.error for ${localFileName}`);
          console.error(error);
          this.fileRequestedAlert = false;
          this.downloadError = true;
        });
    },

    updatePlaybackSpeed() {
      // get frame rate from _fps24_ part of the URL
      let fps = this.vidUrl.match(/_fps(\d+)_/);
      // calculate the coefficient
      let coeff = fps.length > 1 ? fps[1] / 30 : 0.1;

      let x = this.$refs.dialogVideo;
      if (x) {
        x.playbackRate = coeff;
      }
    },

    closeDialog() {
      // the dialog will close if ?vid=... is removed
      let q = Object.assign({}, this.$route.query);
      if (q.vid) {
        delete q.vid;
        this.$router.replace({ query: q });
      }
    }
  }
};
</script>