<template>
  <div>
    <media-dialog :dev-id="devIdComputed" :dialog="dialog"/>

    <v-card v-if="hydrated">
      <v-card-title>
        <v-container>
          <v-layout row>
            <v-flex xs1 md6 class="pa-0">
              <v-spacer></v-spacer>
            </v-flex>
            <v-flex xs10 md5 class="pa-0">
              <v-text-field
                v-model="plateSearch"
                label="Plate search"
                single-line
                hide-details
                clearable
                mask="NNNNNNNN"
                @click:clear="searchByPlateCancel()"
                @change="searchByPlateChange()"
              ></v-text-field>
            </v-flex>
            <v-flex xs1 class="pa-0">
              <v-btn
                icon
                @click="searchByPlate()"
                :disabled="this.plateSearch  && this.plateSearch == this.plateComputed"
              >
                <v-icon class="fal">fa-search</v-icon>
              </v-btn>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-title>
      <v-data-table
        :headers="headersComputed"
        :items="filteredlistMediaUploadsWithSpeedItems"
        disable-initial-sort
        class="elevation-1"
        item-key="name"
        :rows-per-page-items="[10, 50, {text:'All',value:-1}]"
        :loading="loadingColor"
      >
        <template
          slot="items"
          slot-scope="props"
          v-if="props.item.speed > 0 && props.item.type.includes('jpg')"
        >
          <tr class="speed-row">
            <td
              class="text-xs-center pa-0"
            >{{ props.item.timestamp | tsToMMMDDHHmm }}</td>
            <td
              class="text-xs-center pa-0"
            >{{ props.item.speed }}km/h</td>
            <td
              class="text-xs-center pa-0" :class="{ npbg: props.item.name.includes('_bb') }"
            >
              {{ props.item.plate }}
              <div v-if="props.item.name.includes('_bb')">
                <a :href="`https://${mediaBaseDomain}/np/${devIdComputed}/${props.item.name}.jpg`" target="_blank" title="View the plate">
                <img
                  class="plate"
                  :src="`https://${mediaBaseDomain}/np/${devIdComputed}/${props.item.name}.jpg`"
                >
                </a>
              </div>
            </td>
            <td
              class="text-xs-center clickable pa-0"
              @click.stop="openMediaDialog(props.item.timestamp);"
            >
              <v-icon
                class="ml-2 fal clickable accent--text"
                v-if="isVideoThumb(props.item.eventTimestamp)"
              >fa-film</v-icon>
              <v-icon
                class="ml-2 fal clickable"
                v-else-if="props.item.fileReqUtc"
                color="blue-grey"
              >fa-download</v-icon>
              <v-icon v-else class="ml-2 clickable accent--text">photo</v-icon>
              <router-link
                :to="{name:'device-plate',params:{devId:devIdComputed, plate:props.item.plate}}"
                v-if="props.item.plate && !plateComputed"
              >
                <v-icon small class="ml-2 mb-1 fal clickable accent--text">fa-filter</v-icon>
              </router-link>
              <v-icon v-else small class="ml-2 fal clickable invisible">fa-filter</v-icon>
            </td>
          </tr>
        </template>

        <template slot="no-data">
          <v-alert :value="hydrated && !$apollo.loading" type="warning">
            <span v-if="this.plateComputed">
              There is no data for plate
              <strong>{{ plateComputed }}</strong>.
            </span>
            <span v-else>This date has only speed data. Other dates may have photo and video media.</span>
          </v-alert>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>



<script>
import appSyncConfig from "@/config";

import {
  LIST_MEDIA_UPLOADS_WITH_SPEED,
  LIST_MEDIA_UPLOADS_BY_PLATE
} from "@/assets/graph-ql.js";
import MediaDialog from "@/components/MediaDialog.vue";
import * as moment from "moment";

const APOLLO_POLL_INT = 5000; // apollo poll interval (ms) while waiting for media to download
const MEDIA_DOWNLOAD_POLL_DURATION = 30000; // how long to poll for (ms) while waiting for a media file to download

let tableHeaders = {
  search: "",
  headers: [
    {
      text: "Date/time",
      value: "timestamp",
      align: "center",
      sortable: true,
      class: "pa-0"
    },
    {
      text: "Speed",
      value: "speed",
      align: "center",
      sortable: true,
      class: "pa-0"
    },
    {
      text: "Plate",
      value: "plate",
      align: "center",
      sortable: true,
      class: "pa-0"
    },
    { text: "", value: "name", align: "center", class: "pa-0" }
  ]
};

export default {
  name: "SpeedTable",
  props: { devId: String },
  components: { MediaDialog },
  beforeDestroy() {
    if (this.apolloPollingTimer) clearInterval(this.apolloPollingTimer);
  },
  data: function() {
    return {
      hydrated: false,
      dialog: 0, // open / close media dialog
      plateSearch: "", // plate number searched for
      apolloStopPollingAt: null, // a moment() when to stop polling
      apolloPollingTimer: "" // a container for a polling timer handle
    };
  },
  watch: {
    mediaRequestTs() {
      this.apolloStopPollingAt = moment().add(
        MEDIA_DOWNLOAD_POLL_DURATION / 1000,
        "seconds"
      );

      // clear the timer in case there is one already
      if (this.apolloPollingTimer) clearInterval(this.apolloPollingTimer);

      // create a new timer to stop polling
      this.apolloPollingTimer = setInterval(
        this.checkPollingStatus,
        MEDIA_DOWNLOAD_POLL_DURATION
      );

      if (this.plateSearch) {
        this.$apollo.queries.dataByPlate.startPolling(APOLLO_POLL_INT);
      } else {
        this.$apollo.queries.data.startPolling(APOLLO_POLL_INT);
      }
    }
  },
  computed: {
    videoDialogWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "100%";
        case "sm":
          return "100%";
        case "md":
          return "100%";
      }
      return "800px";
    },

    devIdComputed: function() {
      // Get device ID from the property first
      if (this.devId) {
        return this.devId;
      }
      // Copy the URL param into property and use that
      if (this.$route.params.devId) {
        return this.$route.params.devId;
      }
    },
    
    mediaBaseDomain: function() {
      return appSyncConfig.mediaBaseDomain
    },

    plateComputed: function() {
      if (this.$route.params.plate) {
        const np = this.$route.params.plate.toUpperCase();
        this.plateSearch = np;
        return np;
      }
    },

    listMediaUploads: function() {
      if (this.data) {
        return this.data.listMediaUploadsWithSpeed;
      } else {
        return this.dataByPlate.listMediaUploadsByPlate;
      }
    },

    headersComputed: function() {
      if (this.data) {
        return this.data.headers;
      } else {
        return this.dataByPlate.headers;
      }
    },

    filteredlistMediaUploadsWithSpeedItems() {
      return this.listMediaUploads.items.filter(item => {
        return item.type.includes("jpg");
      });
    },

    loadingColor() {
      if (this.$apollo.loading) {
        return "accent";
      } else {
        return false;
      }
    },

    mediaRequestTs: function() {
      return this.$store.state.lastMediaUploadsReqTsUtc;
    }
  },
  methods: {
    isVideoThumb(eventTimestamp) {
      for (var item of this.listMediaUploads.items) {
        if (
          (item.timestamp == eventTimestamp ||
            item.eventTimestamp == eventTimestamp) &&
          item.type.includes("mp4")
        ) {
          return true;
        }
      }
      return false;
    },

    searchByPlate: function() {
      if (!this.plateSearch) return; // there is no data to search for
      this.$router.push({
        name: "device-plate",
        params: { devId: this.devIdComputed, plate: this.plateSearch }
      });
    },

    searchByPlateCancel: function() {
      if (!this.plateComputed) return; // not on a plate-search page
      this.$router.push({
        name: "device",
        params: { devId: this.devIdComputed }
      });
    },

    searchByPlateChange: function() {
      // @change event handler
      if (this.plateSearch) {
        this.searchByPlate();
      } else {
        this.searchByPlateCancel();
      }
    },

    checkPollingStatus: function() {
      // check if the stop polling time was reset, but it's still polling
      if (!this.apolloStopPollingAt) {
        // this is not very efficient to do every time a query runs, but is safer
        // to stop polling on both just in case
        this.$apollo.queries.data.stopPolling();
        this.$apollo.queries.dataByPlate.stopPolling();
        return;
      }

      // we have both polling stop time and pollingInt
      // check if it's time to stop polling
      if (moment().isAfter(this.apolloStopPollingAt)) {
        this.apolloStopPollingAt = null;
        // stop polling on both just in case
        this.$apollo.queries.data.stopPolling();
        this.$apollo.queries.dataByPlate.stopPolling();
        if (this.apolloPollingTimer) clearInterval(this.apolloPollingTimer);
        return;
      }
    },

    openMediaDialog: function(ts) {
      // set ?vid=[vid timestamp] to enable the dialog
      let q = Object.assign({}, this.$route.query);
      q.vid = ts;
      this.$router.replace({ query: q });
    }
  },

  apollo: {
    data: {
      query: () => LIST_MEDIA_UPLOADS_WITH_SPEED,
      variables() {
        this.hydrated = false;
        this.$store.commit("setDbMediaUploadsWithSpeed", {}); // reset the shared store to clear the gallery
        const ts = this.$store.state.currentDateRange
          .replace(/-/g, "")
          .replace(/:/g, "")
          .split(" ");
        return {
          device: this.devId,
          timestampFrom: ts[0],
          timestampTo: ts[1]
        };
      },
      skip() {
        if (this.plateComputed) {
          return true;
        } else {
          return false;
        }
      },
      update(data) {
        this.checkPollingStatus();
        this.$store.commit(
          "setDbMediaUploadsWithSpeed",
          data.listMediaUploadsWithSpeed
        );
        tableHeaders = Object.assign({}, tableHeaders, data);
        this.hydrated = true;
        return tableHeaders;
      }
    },

    dataByPlate: {
      query: () => LIST_MEDIA_UPLOADS_BY_PLATE,
      variables() {
        return {
          device: this.devId,
          plate: this.plateComputed
        };
      },
      skip() {
        if (!this.plateComputed) {
          return true;
        } else {
          return false;
        }
      },
      update(data) {
        this.checkPollingStatus();
        // sort them by date
        data.listMediaUploadsByPlate.items.sort((a, b) =>
          a.timestamp > b.timestamp ? -1 : b.timestamp > a.timestamp ? 1 : 0
        );
        this.$store.commit(
          "setDbMediaUploadsWithSpeed",
          data.listMediaUploadsByPlate
        );
        tableHeaders = Object.assign({}, tableHeaders, data);
        this.hydrated = true;

        return tableHeaders;
      }
    }
  }
};
</script>