/* Analytics, Error reporting and other monitoring scripts are initialized here. */

import Vue from 'vue'
import * as Sentry from '@sentry/browser';
import * as Integrations from '@sentry/integrations';
import VueMatomo from "vue-matomo"
import router from '@/router'
import config from '@/config'

// init error tracking via Sentry if the endpoing was configured
if (config.SentryDSN) {
  Sentry.init({
    dsn: config.SentryDSN,
    integrations: [new Integrations.Vue({ Vue, attachProps: true })],
  });
}

// init user analytics if the endpoing was configured
if (config.MatomoHost && config.MatomoSiteId) {
  Vue.use(VueMatomo, {
    host: config.MatomoHost,
    siteId: config.MatomoSiteId,
    router: router,
    requireConsent: false,
    trackerFileName: "matomo",
    debug: true
  });

  let currentUrl = location.href;
  window.addEventListener('hashchange', function () {
    window._paq.push(['setReferrerUrl', currentUrl]);
    currentUrl = '' + window.location.hash.substr(1);
    window._paq.push(['setCustomUrl', currentUrl]);
    window._paq.push(['setDocumentTitle', 'My New Title']);

    // remove all previously assigned custom variables, requires Matomo (formerly Piwik) 3.0.2
    window._paq.push(['deleteCustomVariables', 'page']);
    window._paq.push(['setGenerationTimeMs', 0]);
    window._paq.push(['trackPageView']);

  });


}

