import { DEV_SPEED_QUERY } from "@/assets/graph-ql.js";
import SpeedChartLine from "@/components/SpeedChartLine.vue";
import ChartJsPluginDataLabels from "chartjs-plugin-datalabels";
export default {
  name: "AccelerationChart",
  components: {
    SpeedChartLine: SpeedChartLine,
    ChartJsPluginDataLabels: ChartJsPluginDataLabels
  },
  props: {
    devId: String,
    timestampFrom: String,
    timestampTo: String
  },
  data: function data() {
    return {
      hydratedGraph: false
    };
  },
  methods: {},
  apollo: {
    data: {
      query: function query() {
        return DEV_SPEED_QUERY;
      },
      variables: function variables() {
        this.hydratedGraph = false;
        return {
          device: this.devId,
          timestampFrom: this.timestampFrom,
          timestampTo: this.timestampTo
        };
      },
      skip: function skip() {
        this.hydratedGraph = false;
        if (!this.timestampFrom || !this.timestampTo) return true;
      },
      update: function update(data) {
        var x = {
          chartOptions: chartOptionsInit,
          chartData: {
            labels: data.listDeviceSpeed.items.map(function (item) {
              return "speed at " + item.timestamp.slice(11, 23);
            }),
            datasets: [{
              data: data.listDeviceSpeed.items.map(function (item) {
                return item.speed;
              })
            }]
          }
        };
        this.hydratedGraph = true;
        return x;
      },
      result: function result() {}
    }
  }
};
var chartOptionsInit = {
  responsive: true,
  maintainAspectRatio: false,
  animation: {
    duration: 0
  },
  hover: {
    animationDuration: 0
  },
  responsiveAnimationDuration: 0,
  legend: {
    display: false
  },
  elements: {
    line: {
      fill: false,
      borderColor: "#283593",
      borderWidth: 2 //lineTension: 0,

    },
    point: {
      pointStyle: "circle",
      backgroundColor: "#283593",
      pointRadius: 5
    }
  },
  scales: {
    yAxes: [{
      display: false,
      ticks: {
        maxTicksLimit: 3
      },
      gridLines: {
        display: true
      }
    }],
    xAxes: [{
      display: false,
      ticks: {
        maxTicksLimit: 3
      },
      gridLines: {
        display: true
      }
    }]
  },
  layout: {
    padding: {
      left: 15,
      right: 15,
      top: 10,
      bottom: 20
    }
  },
  plugins: {
    datalabels: {
      color: "#ffffff",
      font: {
        weight: "bold"
      },
      backgroundColor: "#283593",
      borderRadius: 5
    }
  }
};