<template>
  <div v-if="listMediaUploads && listMediaUploads.items">
    <media-dialog
      :dev-id="devIdComputed"
      :dialog="data.dialog"
    />

    <v-container grid-list-lg fluid justify-space-between pl-0 pr-0>
      <v-layout row wrap>
        <v-flex
          v-for="(item, index) in listMediaUploads.items"
          :class="{displaynone: index >= data.maxItems}"
          :key="item.timestamp"
          md3
          sm6
          xs12
        >
          <v-img
            :src="`https://${mediaBaseDomain}/${devIdComputed}/resized/thumb/${item.name}.jpg`"
            width="100%"
            class="clickable"
            @click.stop="openMediaDialog(item.timestamp);"
          >
            <v-container fill-height fluid pa-0>
              <v-layout align-end justify-start row fill-height ma-0>
                <v-flex xs3 align-start flexbox pa-0>
                  <v-chip
                    small
                    label
                    :color="eventColor(item.eventTimestamp)"
                    class="ma-0 clickable"
                  >
                    {{ item.timestamp | tsToTimeDate }}
                    <v-icon
                      class="ml-2 fal clickable"
                      v-if="isVideoThumb(item.eventTimestamp)"
                      color="white"
                    >fa-film</v-icon>
                    <v-icon class="ml-2 clickable" v-else color="white">photo</v-icon>
                    <span v-if="item.speed" class="ml-2 white--text">{{item.speed}}km/h</span>
                  </v-chip>
                </v-flex>
              </v-layout>
            </v-container>
          </v-img>
        </v-flex>
      </v-layout>

      <v-layout row wrap v-if="data.maxItems < data.imgCount">
        <v-flex xs12>
          <v-btn color="info" @click="data.maxItems += 20">Load more ...</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>



<script>
import appSyncConfig from "@/config";
import dateFormat from "dateformat";
import MediaDialog from "@/components/MediaDialog.vue";

export default {
  name: "MediaForSpeed",
  components: { MediaDialog },
  props: { devId: String },
  data() {
    return {
      data: {
        currentDateRange: "",
        imgCount: -1, // displays no-imgs messge if == 0
        dialog: 0, // video dialog on/off
        maxItems: 12 // maximum number of images in a single block for load more ...
      }
    };
  },
  computed: {
    devIdComputed: function() {
      // Get device ID from the property first
      if (this.devId) {
        return this.devId;
      }
      // Copy the URL param into property and use that
      if (this.$route.params.devId) {
        return this.$route.params.devId;
      }
    },
    
    mediaBaseDomain: function() {
      return appSyncConfig.mediaBaseDomain
    },

    galleryLoadingClass() {
      if (!this.$apollo.loading) return "invisible";
    },

    listMediaUploads: function() {
      // get the number of items, if any
      let itemQty = 0;
      if (this.$store.state.dbMediaUploadsWithSpeed.items)
        itemQty = this.$store.state.dbMediaUploadsWithSpeed.items.length;
      this.data.imgCount = itemQty;

      // the data is fetched from the DB in another component
      // and is shared with this one via Vuex
      return this.$store.state.dbMediaUploadsWithSpeed;
    }
  },
  methods: {
    eventColor(itemEventTimestamp) {
      if (itemEventTimestamp) {
        this.data.eventsPresent = true;
        return "rgba(255,50,50,0.6)";
      } else {
        return "rgba(255,255,255,0.6)";
      }
    },

    isVideoThumb(eventTimestamp) {
      for (var item of this.listMediaUploads.items) {
        if (
          (item.timestamp == eventTimestamp ||
            item.eventTimestamp == eventTimestamp) &&
          item.type.includes("mp4")
        ) {
          return true;
        }
      }
      return false;
    },

    openMediaDialog: function(ts) {
      // set ?vid=[vid timestamp] to enable the dialog
      let q = Object.assign({}, this.$route.query);
      q.vid = ts;
      this.$router.replace({ query: q });
    }
  }
};
</script>