<template>
  <div>
    <v-container fluid pl-0 pr-0 mt-4 mb-0 pb-0>
      <v-layout row xs12 wrap>
        <v-flex grow text-sm-left>
          <v-switch label="Gallery" v-model="data.showGallery"></v-switch>
          <v-spacer></v-spacer>
        </v-flex>

        <v-flex
          v-if="data.showGallery"
          :shrink="$vuetify.breakpoint.smAndUp"
          :text-xs-center="$vuetify.breakpoint.xsOnly"
          :xs12="$vuetify.breakpoint.xsOnly"
        >
          <v-btn icon @click="refreshToNext(-1)">
            <v-icon small class="fal">fa-angle-double-left</v-icon>
          </v-btn>

          <v-btn outline @click.stop="toggleCal()">
            <v-icon small class="fal pr-2">fa-calendar</v-icon>
            {{ this.data.dateShowing | tsToDateTextMonth }}
          </v-btn>
          <v-btn icon @click="refreshToNext(1)">
            <v-icon small class="fal">fa-angle-double-right</v-icon>
          </v-btn>
        </v-flex>
        <v-flex
          v-if="data.showGallery"
          :shrink="$vuetify.breakpoint.smAndUp"
          :text-xs-center="$vuetify.breakpoint.xsOnly"
          :xs12="$vuetify.breakpoint.xsOnly"
        >
          <v-btn
            icon
            @click="data.eventsOnly = !data.eventsOnly"
            :disabled="!this.data.eventsPresent"
            :color="(this.data.eventsOnly)? 'error' : 'white'"
          >
            <v-icon
              small
              class="fal"
              :color="(this.data.eventsOnly)? 'white' : 'error'"
            >fa-exclamation-triangle</v-icon>
          </v-btn>

          <v-btn icon @click="refreshToToday()">
            <v-icon small class="fal">fa-sync</v-icon>
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>

    <v-container grid-list-lg fluid justify-space-between pl-0 pr-0 v-if="data.showGallery">
      <div :class="galleryLoadingClass">
        <v-progress-linear height="2" indeterminate color="blue-grey" class="my-0 py-0"></v-progress-linear>
      </div>

      <v-alert
        :value="data.imgCount == 0"
        type="warning"
      >No images for {{ this.data.dateShowing | tsToDateTextMonth }}</v-alert>

      <v-expand-transition>
        <v-date-picker
          color="accent"
          v-if="data.showCal"
          v-model="data.dateShowing"
          full-width
          landscape
          class="mt-3"
          first-day-of-week="1"
          :max="this.$store.state.today"
        ></v-date-picker>
      </v-expand-transition>

      <v-layout row wrap>
        <v-flex v-for="item in filteredlistMediaUploadsItems" :key="item.timestamp" md3 sm6 xs12>
          <a
            v-if="(!data.eventsOnly || item.eventTimestamp)"
            target="_blank"
            :href="`https://${mediaBaseDomain}/${devIdComputed}/resized/fhd/${item.name}.jpg`"
          >
            <v-img
              :src="`https://${mediaBaseDomain}/${devIdComputed}/resized/thumb/${item.name}.jpg`"
              width="100%"
            >
              <v-container fill-height fluid pa-0>
                <v-layout align-end justify-start row fill-height ma-0>
                  <v-flex xs3 align-start flexbox pa-0>
                    <v-chip
                      small
                      label
                      :color="eventColor(item.eventTimestamp)"
                      class="ma-0"
                      ma-0
                    >{{ item.timestamp | tsToTimeDate }}</v-chip>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-img>
          </a>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>



<script>
import appSyncConfig from "@/config";
import dateFormat from "dateformat";
import { LIST_MEDIA_UPLOADS } from "@/assets/graph-ql.js";

export default {
  name: "MediaForStills",
  props: { devId: String },
  data() {
    return Object.assign({}, dt);
  },
  
  created() {
    // make sure the data is queried even if the component was re-used
    if (this.data && this.data.querySkip) this.data.querySkip = false;
    // restore the last gallery setting
    let name = this.$options.name + "data.showGallery";
    const that = this;
    this.$getItem(name).then(function(dbVal) {
      if (dbVal == false) {
        that.data.showGallery = false; 
      } else {
        that.data.showGallery = true; // default to TRUE
      }
    });
  },
  computed: {
    devIdComputed: function() {
      // Get device ID from the property first
      if (this.devId) {
        return this.devId;
      }
      // Copy the URL param into property and use that
      if (this.$route.params.devId) {
        return this.$route.params.devId;
      }
    },

    mediaBaseDomain: function() {
      return appSyncConfig.mediaBaseDomain
    },

    galleryLoadingClass() {
      if (!this.$apollo.loading) return "invisible";
    },

    filteredlistMediaUploadsItems() {
      // only return JPG files (filter out MP4s)
      let filteredList = this.data.listMediaUploads.items.filter(item => {
        return item.type.includes("jpg");
      });
      // update the img count after apollo total record count
      if (this.data.imgCount > 0) this.data.imgCount = filteredList.length;
      return filteredList;
    }
  },

  methods: {
    toggleCal() {
      this.data.showCal = this.data.showGallery ? !this.data.showCal : true;
      this.data.showGallery = true;
    },
    refreshToToday() {
      this.data.querySkip = false;
      this.data.showCal = false;
      this.data.dateShowing = dateFormat(new Date(), "yyyy-mm-dd");
    },
    refreshToNext(days) {
      let nextDate = new Date(this.data.dateShowing);
      nextDate.setDate(nextDate.getDate() + days);

      this.data.querySkip = false;
      this.data.showCal = false;
      this.data.dateShowing = dateFormat(nextDate, "yyyy-mm-dd");
    },
    eventColor(itemEventTimestamp) {
      if (itemEventTimestamp) {
        this.data.eventsPresent = true;
        return "rgba(255,50,50,0.6)";
      } else {
        return "rgba(255,255,255,0.6)";
      }
    }
  },
  watch: {
    "data.dateShowing": function() {
      this.data.querySkip = false;
      this.data.showCal = false;
    },
    "data.showGallery": function(newVal) {
      // persist the selection in the local storage
      let name = this.$options.name + "data.showGallery";
      this.$setItem(name, newVal);
    }
  },
  apollo: {
    data: {
      query: () => LIST_MEDIA_UPLOADS,
      variables() {
        return {
          device: this.devIdComputed,
          limit: 500,
          timestamp: this.data.dateShowing.replace(/-/g, "")
        };
      },
      skip() {
        return this.data.querySkip; // prevent infinite loop
      },
      update(data) {
        this.data.querySkip = true; // prevent infinite loop
        this.data.eventsPresent = false; // reset number of events
        data.imgCount = data.listMediaUploads.items.length; // a shortcut for no-imgs msg
        let dataCombined = Object.assign({}, this.data, data); // copy response data into the instance
        return dataCombined;
      }
    }
  }
};

let dt = {
  data: {
    dateShowing: dateFormat(new Date(), "yyyy-mm-dd"), // the current date on display
    querySkip: false, // set to true to break infinite reload loop
    showCal: false, // display calendar
    showGallery: true, // display the gallery container
    imgCount: -1, // displays no-imgs messge if == 0
    eventsPresent: false, // number of imgs with events
    eventsOnly: false, // display only images with events
    listMediaUploads: {
      items: []
    }
  }
};
</script>